    .search-mobile-form-in {
        width: 100%;
        padding: 0 40px;
    }
    .search-mobile-form input {
        height: 60px;
        font-size: 23px;
    }
    .search-mobile-form .search-mobile-form-close {
        position: absolute;
        right: 10px;
